	components: {
		ShopList
	},
	data() {
		return {
			cateList: []
		}
	},
	async mounted(){
		this.cateList = await request('/getCateList')
	},
	watch: {
		navType(newValue){
			console.log('xx', newValue)
		}
	},
	computed: {
		cateObj(){
			return this.cateList.find(item => item.category.parentId === this.navId)
		}
	}
}
